::selection, .text-selected {
  background-color: $primary;
  color: color-yiq($primary);
}

@include scrollbars(.4em, rgba($gray-600, .5), transparent);

@font-face {
    font-family: 'Inter';
    src: url('../fonts/Inter-SemiBold.woff') format('woff');
    font-weight: bold;
    font-style: normal;
}

@font-face {
    font-family: 'Inter';
    src: url('../fonts/Inter-Regular.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}

body {
  padding-top: 55px;
}

// SVG Icons
#svg-sprite {
  position: absolute;
  visibility: hidden;
  top: -9999999px;
  z-index: -1;
}

.svg-icon {
  height: 1rem;
  width: 1rem;
  fill: currentColor;
  display: inline-block;
  vertical-align: middle;

  &.svg-md {
    height: 1.5rem;
    width: 1.5rem;
  }

  &.svg-lg {
    height: 2rem;
    width: 2rem;
  }
}

.hljs {
  display: block;
  overflow-x: auto;
  padding: 1rem 2rem;
  border-radius: $border-radius;
  color: #090910;
  background: #fbfbfd;
  box-shadow: 0 1px 1px rgba(0,0,0,.075);
  border: 0;
  margin: 0;
  font-size: $font-size-base;
}

.hljs-comment,
.hljs-quote {
  color: $gray-500;
}

.hljs-doctag,
.hljs-keyword,
.hljs-formula {
  color: #055472;
}

.hljs-section,
.hljs-name,
.hljs-selector-tag,
.hljs-deletion,
.hljs-subst {
  color: #ca473f;
}

.hljs-literal {
  color: #56b6c2;
}

.hljs-string,
.hljs-regexp,
.hljs-addition,
.hljs-attribute,
.hljs-meta-string {
  color: #690;
}

.hljs-built_in,
.hljs-class .hljs-title {
  color: #e6c07b;
}

.hljs-attr,
.hljs-variable,
.hljs-template-variable,
.hljs-type,
.hljs-selector-class,
.hljs-selector-attr,
.hljs-selector-pseudo,
.hljs-number {
  color: #d19a66;
}

.hljs-symbol,
.hljs-bullet,
.hljs-link,
.hljs-meta,
.hljs-selector-id,
.hljs-title {
  color: #ca473f;
}

.hljs-emphasis {
  font-style: italic;
}

.hljs-strong {
  font-weight: bold;
}

.hljs-link {
  text-decoration: underline;
}

.page-header {
  font-size: $h2-font-size;
  padding-bottom: 0.5rem;
  margin: 0 0 1rem;
  color: $black;
}

.page-postheader {
  font-size: $h4-font-size;
  padding-bottom: 0.5rem;
  margin: 1rem 0 0;
  color: $black;
}

.page-subheader {
  font-size: $h5-font-size;
  padding-bottom: 0.5rem;
  margin: 1rem 0 0;
  color: $black;
}

.requirements {
  display: flex;
  flex-wrap: wrap;
  padding: 0;
  li {
    width: 31%;
    text-align: center;
    margin: .5rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 1.5rem 0.6rem;
    background: $dark;
    color: $white;
    min-height: 135px;
    @include border-radius($border-radius);
    @include box-shadow($box-shadow);

    @include hover {
      opacity: .8;
    }

    .or {
    }

    &.php {
      background: $teal;
    }

    &.apache {
      background: $blue;
    }

    &.mysql {
      background: $indigo;
    }

    &.ext {
      background: $pink;
    }

    &.pdo {
      background: $orange
    }

    span {
      display: block;
      text-align: center;
    }
  }

  code {
    color: $white;
    background: rgba(0, 0, 0, 0.7);
    padding: 0.2rem .3rem;
    border-radius: $border-radius;
  }

}



#content {

  a {
    text-decoration: underline;
  }
}

.sidebar-bg {
  background: linear-gradient(0deg,#fff,#f5f5fa) fixed;
}

#sidebar {
  max-width: 250px;
  margin: auto;
  padding: 32px 24px 24px;
  height: 100%;
  ul {
    li {
      margin-bottom: 0.5rem;
      .heading {
        margin: 0;
        font-weight: 600;
        padding: 0.2rem 0;
        color: $dark;
        text-transform: uppercase;
        font-size: .9rem;
        .svg-icon {
          height: 1.1rem;
          width: 1.1rem;
        }
      }
      a {
        color: #090910;
        transition: color 200ms ease 0s;
        display: block;
        font-weight: 500;
        margin-bottom: .5rem;
        @include hover-focus {
          color: $link-color;
        }
      }

      &.active {
        a {
          border-color: $link-color;
          color: $link-color;
          font-weight: $font-weight-bold;
        }
      }
    }
  }
}

#sidebar-menu {
  position: fixed;
  width: inherit;
}

.navbar-brand img {
  max-width: 100px;
  height: auto;
}

.wrapper {
  margin: 0 auto;
  background: $white;
  min-height: 100vh;
  height: 100%;
}

#content {
  max-width: 900px;
  margin: 0 auto;
  word-spacing: .1px;


  section {
    border-bottom: 1px solid $gray-400;
    padding-bottom: 1.5rem;
    margin-bottom: 1.5rem;
    &:last-child {
      border: 0;
      padding: 0;
      margin: 0;
    }
  }

  p {
        line-height: 1.4rem;
    margin: 0 0 0.8rem;
    letter-spacing: .3px;
  }

  ul {
    &.casual-list {
      @extend .list-unstyled;
      margin: 0 .3rem 1.5rem;
      li {
        margin-bottom: 0.5rem;
      }
    }

  }

  figure {
    position: relative;
    text-align: center;
    background: $gray-100;
    color: $text-muted;
    margin: .5rem 0 1rem;
    display: inline-block;
    border: 1px solid $light;
    padding: 0.5rem;
    @include border-radius($border-radius);
    img {
      max-width: 100%;
      height: auto;
    }

    figcaption {
      font-size: 80%;
      padding: .2rem .5rem;
      margin-top: .3rem;
      line-height: 1rem;
    }
  }
}

// Styles for mobile
@media (max-width: 768px) {
  .wrapper {
    border: 0;
    margin: 0;
    padding: 0;
    max-width: 100%;
  }

  #sidebar {
    border: 0;
  }

  #sidebar-menu {
    position: static;
  }

  .requirements {
    li {
      width: 100%;
    }
  }
}

.footer-logo {
  max-width: 130px;
  height: auto;
}

.docs-logo {
  max-width: 125px;
  height: auto;
}
